import {request} from './request'


// 
export function GetSong(keywords){
  return request({
    // url:'/cloudsearch',
    url:'/juheapi/getmusic',
    params:{
      keywords,
      limit:5
    }
  })
}

// 
export function GetPlayMisicUrl(id){
  return request({
    // url:'/song/url',
    url:'/juheapi/geturl',
    params: {
      id
    }
  })
}

//  
export function GetPlayMisicLyric(id){
  return request({
    // url:'/lyric',
    url:'/juheapi/getlyric',
    params: {
      id
    }
  })
}

