<template>
  <div id="music">
    <!--  -->
     <div class="search bar7">
        <div>
            <el-input type="text" placeholder="" style="margin-top: 0"
            name="crid" @keyup.enter="playMusic(songName)" v-model="songName" />
            <button type="submit" id="searchMusic" @click="playMusic(songName)"></button>
        </div>
    </div>
    <!-- <el-input ></el-input> -->
     <!--music list showlrc-->
     <aplayer :music="audio[0]" :list="audio"  :showlrc="true" :theme="mytheme"></aplayer>
  </div>
</template>

<script>
import aplayer from "vue-aplayer"; 
import {GetSong,GetPlayMisicUrl,GetPlayMisicLyric} from "@/musicNetWork/music.js"; 
  export default {
    data() {
      return {
        songName:'',     
         // 
        audio:  [
          // 
            {
                title:'',
                artist: 'CMJ',
                url: 'https://rt01-sycdn.kuwo.cn/7b5137bac33d558e774c26a95ed38bf4/6077019a/resource/n1/18/25/1394031454.mp3',
                pic: "http://p4.music.126.net/M34HFzLO2xhDLuX_zEALKA==/109951164291347934.jpg?param=100y100" ,
                lrc: '[00:00.00] (,, . ,,) ',
            },
        ],
        mytheme:'#b7daff'
      }
    },
    methods: {
      playMusic(songName){
        this.songName = ''
        // 
        let songDataArray = []
        GetSong(songName).then(res => {
          console.log(res.result.songs);
          // 
          res.result.songs.forEach( async (item,index,array) => {
            // 
            let songData = {}
            // 
            songData.title = item.name
            // 
            songData.artist = item.ar[0].name
            // 
            for(var key in  item.al){
              songData.pic = item.al.picUrl + '?param=100y100'
            }
            // 
            let musicUrl = await GetPlayMisicUrl(item.id)
            songData.url = musicUrl.data[0].url            
            // 
            let MisicLyric = await GetPlayMisicLyric(item.id)
            if(MisicLyric.nolyric){
              songData.lrc = '[00:00.00] QAQ'
            }else {
              songData.lrc = MisicLyric.lrc.lyric
            }
            //  push
             songDataArray.push(songData)
      }
       );
        })
        // audio,
        this.audio = songDataArray
      }
    },
    components:{
      aplayer
    },
    mounted(){
      // 
      let searchMusic = document.querySelector('#searchMusic');
        searchMusic.click();
    }
  }
</script>

<style lang="scss" scoped>
* {
	box-sizing:border-box;
}
// 
.aplayer {
  margin: 0;
  margin-top: 5px;
  border-radius: 5px;
}
#container {
	width:500px;
	height:820px;
	margin:0 auto;
}
div.search {
	padding:10px 0;
  // transform: translateY(20px);
}
div {
	position:relative;
	margin:0 auto;
}
input,button {
	border:none;
	outline:none;
}
input {
	width:100%;
	height:42px;
	padding-left:13px;
}
button {
	height:42px;
	width:42px;
	cursor:pointer;
	position:absolute;
}
/*7*/
.bar7 div {
	height:30px;
}
.bar7 input {
	width:250px;
	border-radius:42px;
	border:2px solid #324B4E;
	background:#F9F0DA;
	transition:.3s linear;
	float:right;
}
.bar7 input:focus {
	width:300px;
}
.bar7 button {
	background:none;
	top:-2px;
	right:20px;
}
.bar7 button:before {
	content:"Search";
	font-family:FontAwesome;
	color:#324b4e;
}

</style>